import { COLORS } from '@src/configs';
import * as React from 'react';

import {
  Col,
  ColPicked,
  HomeContainer,
  MovieTitle,
  Row,
  TicketStatus,
  TicketWrapper,
} from './home.styled';
import { useAppContext } from '../../context/app.context';

type IHomeProps = {};
const Home = (_: IHomeProps) => {
  const [state] = useAppContext();
  const [selected, setSelected] = React.useState({});
  const onTicketSelect = React.useCallback(
    (i, j) => {
      if (selected[`${i}_${j}`]) {
        setSelected({ ...selected, [`${i}_${j}`]: 0 });
      } else {
        setSelected({ ...selected, [`${i}_${j}`]: 1 });
      }
    },
    [selected, state]
  );
  const { types, detail } = state.meta;
  return (
    <HomeContainer>
      <MovieTitle>
        <h2>Mắt biếc</h2>
      </MovieTitle>
      <TicketWrapper>
        {state.tickets.map((item: any, rowIndex: number) => {
          const seatRow: string = String.fromCharCode(65 + rowIndex);
          return (
            <Row key={'row' + rowIndex}>
              <span>{seatRow}</span>
              {item.map((_item: any, colIndex: number) => {
                if (_item.isPicked) {
                  return <ColPicked />;
                }
                return (
                  <Col
                    key={'col' + colIndex}
                    onClick={() => onTicketSelect(rowIndex, colIndex)}
                    type={_item.type}
                    selected={!!selected[`${rowIndex}_${colIndex}`]}>
                    {!!selected[`${rowIndex}_${colIndex}`]
                      ? `${seatRow} ${colIndex + 1}`
                      : ''}
                  </Col>
                );
              })}
            </Row>
          );
        })}
      </TicketWrapper>
      <TicketStatus>
        <ul>
          {types.map((item: string) => {
            return (
              <li style={{ color: COLORS[item].default }}>
                {item}: {detail[item].price}
              </li>
            );
          })}
        </ul>
      </TicketStatus>
    </HomeContainer>
  );
};
export default Home;
